/* eslint-disable import/first */
import { FormEvent, useEffect, useState } from "react";
import BaseInput from "../../components/baseInput";
import { CarProps, formInputPros } from "../../includes/types";
import SUV from "../../assets/images/cars/suv.png";
import Px from "../../assets/images/px.png";
import { useHistory } from "react-router-dom";
import { SectionBlackComponent } from "../../components/footer/sectionTwo";
import { AppRoutes } from "../../includes/constant";
import useHttpHook from "../../includes/useHttpHook";
import BaseLoader from "../../components/baseLoader";

const CarDetailsPage = ()=>{

    const {GetCarDetails,loading} = useHttpHook();
    
    const [details,setDetails] = useState<CarProps>({
        logo:"https://upload.wikimedia.org/wikipedia/commons/thumb/e/e8/Tesla_logo.png/600px-Tesla_logo.png",
        id:`3`,
        images:[SUV],
        model:"Toyota SUV",
        type:"SUV",
        year:"2020",
        transmission:"Automatic",
        fuel:"fuel",
        country:"Canada",
        city:"Montreal",
        size:"SUV",
        battery:"7 kWh"
    })
   const history = useHistory();
   const [selectedIndex,setSelectedIndex] = useState<number>(0)

useEffect(()=>{
   var getId:string[] = String(window.location.pathname).split("/")
    const id = getId[getId.length - 1]
    GetCarDetails(String(id)).then((res)=>{
        if(res.status && res.data?.car)
            {
        return setDetails(res.data.car)
            }
       history.replace(AppRoutes.Cars)
    })
    window.scrollTo({top:0,behavior:"smooth"})
},[])

    return <div className="neutral">
    <div className="container py-5">
        <div className="row mt-5">
            <div className="col-lg-6">
                    <button
                    onClick={()=>{
                        history.goBack()
                    }}
                    className="btn p-0 m-0"
                    >
                <NextBtnWithArrow />
                    </button>
             </div>
        </div>
       {loading ?<div className="mimHeight-100">
        <BaseLoader />
       </div>:<>
        <div className="d-flex mt-5">
        <div >
            <img 
            src={details?.logo} 
            className="c-logo"
            style={{backgroundColor:!details.logo?"#eee":"transparent"}}
            />
        </div>
        <div className="ps-3" >
            <div className="ff-inter fs-32">{details.model}</div>
            <div className="c-year-2">{details.year}</div>
        </div>
        </div>
        <div className="c-car-1 my-3" style={{backgroundColor:details.images.length === 0?"#eee":"transparent"}}>
            {details.images?.filter((a,i)=>i === 0).map((a,i)=><img key={i} src={a} />)}
        </div>
        <div className="row">
            <div className="c-tab-wrp col-lg-4" >
                <div 
                onClick={()=>{
                    setSelectedIndex(0)
                }}
                className={`c-tab${selectedIndex === 0?"-active":""} btn`}>
                Overview
                </div>
                <div 
                 onClick={()=>{
                    setSelectedIndex(1)
                }}
                className={`c-tab${selectedIndex === 1?"-active":""} btn`}>
                Financial plans
                </div>
            </div>
        </div>
        {selectedIndex === 0?<SectionOne {...details} />:<SectionTwo />}
        </>}

    </div>
    <SectionBlackComponent />

    </div>
}
export default CarDetailsPage;

