const SectionOne = (props:CarProps)=>{
    return <div className="py-3">
    <div className="ff-inter fs-40 black fw-600 m-fs-20">Find out what's included</div>
    <p className="ff-inter fs-20 gray">Find out which of these plans is more suitable for you and your vehicle. Learn how each options can maximize your earnings. 
    We're here to support you.</p>
    <div className="sub-title-1 fs-20 ff-inter mb-3">
    VEHICLES SPECIFICATIONS
    </div>
    <div className="grid-4">
        {[
            {title:"Year of production",value:props.year},
            {title:"Transmission",value:props.transmission},
            {title:"Fuel",value:props.fuel},
            {title:"Battery capacity",value:props.battery},
            {title:"Vehicle size",value:props.size},
        ].map((a,i)=><div key={i} className="">
            <div className="card-white-2 p-3">
                <div className="cc-value">{a.value}</div>
                <div className="cc-name">{a.title}</div>
            </div>
        </div>)}
    </div>
    <SectionOneComp />
    </div>
}
