import { useState } from "react";
import { APIResponse, ApplyFormObj, CarProps } from "./types";
import { GetDATA, PostDATA } from "./functions";

const useHttpHook = ()=>{
 
    const [loading,setLoading] = useState<boolean>(false);
    const HandleApplySubmit = (props:ApplyFormObj)=>{
        return new Promise<APIResponse>((resolve)=>{
            // console.log(props)
            // return({status:false,message:""})
            // return ;
            setLoading(true)
      
        return PostDATA("application",props,"image").then((response)=>{
            setLoading(false)
            
            resolve(response)
        })
        })
    }
    const HandleInvestorsSubmit  = (props:ApplyFormObj)=>{
        return new Promise<APIResponse>((resolve)=>{
            setLoading(true)
        return PostDATA("inquiry",props).then((response)=>{
            setLoading(false)
            resolve(response)
        })
        })
    }
    const GetCars  = (props:ApplyFormObj)=>{
        return new Promise<APIResponse>((resolve)=>{
            setLoading(true)
            
        return GetDATA("cars",{}).then((response)=>{
            setLoading(false)
            resolve(response)
        })
        })
    }
    
    const GetCarDetails  = (id:string)=>{
        return new Promise<APIResponse>((resolve)=>{
            setLoading(true)
        return GetDATA("car/"+id,{}).then((response)=>{
            setLoading(false)
            resolve(response)
        })
        })
    }
    const GetInquiryTopic  = ()=>{
        return new Promise<APIResponse>((resolve)=>{
            setLoading(true)
        return GetDATA("inquiry-topics",{}).then((response)=>{
            setLoading(false)
            resolve(response)
        })
        })
    }
  
    return {
        GetCarDetails,
        loading,
        HandleApplySubmit,
        HandleInvestorsSubmit,
        GetCars,
        GetInquiryTopic
    }
}
export default useHttpHook;